.title-container {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    text-align: left;
    margin-bottom: 20px;
  }

  .title {
    font-size: 24px;
  }

 /* Media query for smaller screens */
 @media screen and (max-width: 1024px) {
    .title {
      margin-left: 100px;
      margin-right: 100px;
    }
  }